<template>
    <div class="container">
        <b-form @submit="onSubmit" @reset="onReset" v-if="show">
            <b-form-group
                id="input-group-1"
                label="Имя пользователя:"
                label-for="username"
            >
                <b-form-input
                    id="username"
                    v-model="form.username"
                    type="text"
                    placeholder="Username"
                    required
                ></b-form-input>
            </b-form-group>

            <b-form-group
                id="input-group-2"
                label="Пароль:"
                label-for="password"
            >
                <b-form-input
                    id="password"
                    v-model="form.password"
                    type="password"
                    required
                ></b-form-input>
            </b-form-group>
            <b-button type="submit" variant="primary" class="m-2">Войти</b-button>
            <b-button type="reset" variant="danger">сбросить</b-button>
        </b-form>
    </div>
</template>

<script>
export default {
    name: "Registration",
    data() {
        return {
            form: {
                username: '',
                password: '',
            },
            show: true
        }
    },
    methods: {
        onSubmit(event) {
            event.preventDefault()
            fetch('http://127.0.0.1:8000/auth/token/login/', {
                method: 'Post',
                headers: {
                    'Content-Type': 'application/json;charset=utf-8'
                },
                body: JSON.stringify(this.form)
            })
                .then(data => data.json())
                .then(data => localStorage.setItem('Token', data['auth_token']))
                .then(() => fetch('http://127.0.0.1:8000/auth/users/me/', {
                        method: 'GET',
                        headers: {
                            'Content-Type': 'application/json;charset=utf-8',
                            'Authorization': `Token ${localStorage.getItem('Token')}`,
                        },
                    }
                ).then((data) => data.json())
                    .then((data) => console.log(data))
                )
            // alert(JSON.stringify(this.form))
        },
        onReset(event) {
            event.preventDefault()
            // Reset our form values
            this.form.username = ''
            this.form.password = null
            // Trick to reset/clear native browser form validation state
            this.show = false
            this.$nextTick(() => {
                this.show = true
            })
        }
    }
}
</script>

